declare const __SDKVERSION__: string;

export default class VersionManager {
  private static _defaultVersion = new Date().toISOString();

  static get sdkVersion(): string {
    try {
      return __SDKVERSION__;
    } catch (e) {
      return this._defaultVersion;
    }
  }
}